import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, LoadingController, MenuController } from 'ionic-angular';
import { Item } from '../../models/item';
import { Items } from '../../providers';
import { User } from '../../providers';
import { Api } from '../../providers/api/api';

/**
 * Generated class for the MyeventsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-trainings',
  templateUrl: 'trainings.html',
})
export class TrainingsPage {
  currentItems: Item[];
  loaded: boolean;
  emptytrainings: boolean = false;

  constructor(public api: Api, public navCtrl: NavController, public items: Items, public menu: MenuController, public modalCtrl: ModalController, public user: User, public loadingCtrl: LoadingController) {
    
  }

  ionViewCanEnter(){
    let seq = this.api.post('checktoken', []).share();
    seq.subscribe((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (res.error !== true) {
        return true;
      } else {
        this.navCtrl.setRoot('LoginPage');
      } 
    }, err => {
      this.navCtrl.setRoot('LoginPage');
    });
  }

  ionViewDidEnter() {
    // the root left menu should be disabled on the tutorial page
    this.menu.enable(true);
  }
  ionViewWillEnter(): Promise<any>{
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`
    });
    loading.present();
    return new Promise((resolve, reject) => {
      let seq = this.items.querytraining().share();
      seq.subscribe((res: any) => {
          this.currentItems = res;
          resolve(true);
          if (res.length == 0) {
            this.emptytrainings = true;
          } else {
            this.emptytrainings = false;
          }
      }, (err) => {
        resolve(false);
      }, () => { loading.dismiss(); });
    })
  }


  loadItems() {
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`
    });
    loading.present();
    let seq = this.items.querytraining().share();

    seq.subscribe((res: any) => {
      this.currentItems = res;
    }, err => {
      console.log('error get list items');
    }, () => { loading.dismiss(); });
  }
  subscribe(itemid) {
    var data = {'trainingid' : itemid};
    let seq = this.api.post('api/subscribetraining', data).share();
    seq.subscribe((res: any) => {
      this.loadItems();
    }, err => {
      console.error('ERROR', err);
    });
  }
}




